import ErmjPathHelper from "../data/ErmjPathHelper";
import ErmjMjStyleHelper from "../tool/ErmjMjStyleHelper";
import ErmjGameConst from "../data/ErmjGameConst";

const {ccclass, property} = cc._decorator;

@ccclass
export default class ErmjRubMjAnim extends cc.Component {
    @property(cc.Sprite)
    mjFrontSp: cc.Sprite = null;

    private effectSk1: sp.Skeleton;
    private effectSk2: sp.Skeleton;

    protected onLoad(){
        this.effectSk1 = cc.find("content/effect1", this.node).getComponent(sp.Skeleton);
        this.effectSk1.node.active = false;
        this.effectSk2 = cc.find("content/effect2", this.node).getComponent(sp.Skeleton);
        this.effectSk2.node.active = false;
    }

    public doRubAnim(value: number){
        Global.ResourceManager.loadBundleAutoAtlas(ErmjGameConst.Gid, this.mjFrontSp, ErmjPathHelper.gameTexturePath + "mahjong/atlas_mahjong", ErmjMjStyleHelper.mjHandMap[value], null, true);
        this.effectSk1.node.active = true;
        this.effectSk2.node.active = true;
        this.effectSk1.setAnimation(0, "idle", false);
        this.effectSk2.setAnimation(0, "idle_shouzi", false);
    }

    protected onDisable(){
        this.effectSk1.clearTracks();
        this.effectSk2.clearTracks();
        this.effectSk1.node.active = false;
        this.effectSk2.node.active = false;
    }
}
